// 수정페이지
//
//useParams 현재 사이트 위치 알기 위함
import { useParams } from "react-router-dom";

import DiaryEditor from "../components/DiaryEditor";

function Edit() {
  // 현재사이트 id값
  let { id } = useParams();

  return (
    <>
      <DiaryEditor isEdit={true} id={id} />
    </>
  );
}

export default Edit;
