// 일기 리스트 조회

import { useEffect, useState } from "react";
import { Row, Col } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "../css/list.css";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import Diaryitem from "../item/diaryitem";
import Button from "../components/Button";
import Header from "../components/Header";

import { changeOrder, changeEmoOrder, changeIdOrder } from "../Slice/itemSlice";

function DiaryList() {
  useEffect(() => {
    // 페이지 타이틀 바꾸기
    let title = document.getElementsByTagName("title")[0];
    title.innerHTML = `나만의 일기장 - 목록`;
  }, []);

  // 일기 리스트
  let List = useSelector((state) => state.item.item);
  let dispatch = useDispatch();

  // (List의 값을 다시 담는다)필요한 일기 리스트
  let [data, setData] = useState([]);

  // 날짜를 저장하는 데이터
  // 한국기준 시간 지정
  const offset = 1000 * 60 * 60 * 9;
  const time = new Date(new Date().getTime() + offset);
  let [date, setDate] = useState(time);

  // 사이트 이동
  let navigate = useNavigate();

  useEffect(() => {
    // 리스트가 1개 이상 일때만
    if (List.length >= 1) {
      //첫쨋날과 마지막날의 시간 구함
      const firstDay = new Date(
        date.getFullYear(),
        date.getMonth(),
        1
      ).getTime();

      const lastDay = new Date(
        date.getFullYear(),
        date.getMonth() + 1
      ).getTime();

      // 필요한 데이터에 첫날과 마지막날 사이에 있는 데이터를 넣어준다
      setData(List.filter((e) => firstDay <= e.date && e.date < lastDay));
    }
  }, [List, date]);

  return (
    <>
      {/* header */}
      <Header
        //년도와 달 표시
        text={
          <input
            className="day"
            //  년 월만 표시
            value={date.toISOString().slice(0, 7)}
            onChange={(e) => {
              // 다시 초기화 해서 값 변경
              setDate(new Date(e.target.value));
            }}
            type="month"
          />
        }
        //과거
        leftChild={
          <Button
            onClick={() => {
              setDate(
                new Date(
                  date.getFullYear(),
                  date.getMonth() - 1,
                  date.getDate() //몇일
                )
              );
            }}
            text={"<"}
          />
        }
        // 미래
        rightChild={
          <Button
            onClick={() => {
              setDate(
                new Date(
                  date.getFullYear(),
                  date.getMonth() + 1,
                  date.getDate() //몇일
                )
              );
            }}
            text={">"}
          />
        }
      />

      {/* 리스트 조회 */}
      <div className="list-wrap">
        {/* 정렬과 일기 쓰기  */}
        <div className="top">
          <Row>
            <Col className="order" xs={6} xl={3}>
              {/* 최신순 오래된순 정렬 */}
              <select
                onChange={(e) => {
                  dispatch(changeOrder(e.target.value));
                }}
              >
                <option value={"new"}>최신순</option>
                <option value={"last"}>오래된 순</option>
              </select>
              {/*  */}
            </Col>
            <Col className="order" xs={6} xl={3}>
              {/* 감정 좋은순 나쁜순  */}
              <select
                onChange={(e) => {
                  dispatch(changeEmoOrder(e.target.value));
                }}
              >
                <option value={"basics"}>기본(감정 정렬)</option>
                <option value={"good"}>좋은 감정순</option>
                <option value={"bad"}>나쁜 감정순</option>
              </select>
              {/*  */}
            </Col>

            {/*  */}
            <Col className="btn-wrap" xl={6}>
              {/* 일기 작성 */}
              <Button
                onClick={() => {
                  navigate("/newdiary");
                  dispatch(changeIdOrder());
                }}
                text={"새 일기쓰기"}
                type={"myBtn_positive"}
              />
            </Col>
            {/*  */}
          </Row>
        </div>
        {/* 아이템 불러와 리스트 생성 */}
        {data.map((a, i) => {
          return <Diaryitem data={data} i={i} key={i} />;
        })}

        {/*  */}
      </div>
    </>
  );
}

export default DiaryList;
