import { Routes, Route, useNavigate } from "react-router-dom";
import { Navbar, Container, Nav, Row, Col } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import "./css/main.css";

import Main from "./page/main";
