/*eslint-disable*/
// 작성페이지와 수정페이지 공통컴포넌트

import { useState, useRef, useEffect } from "react";
import Button from "./Button";
import Header from "./Header";
//
import "../css/DiaryEditor.css";
//
import { useNavigate } from "react-router-dom";
//
import { useDispatch, useSelector } from "react-redux";
// emotion 아이템
import EmotionItem from "../item/emotionitem";
// weather 아이템
import WeatherItem from "../item/weatheritem";
// slice 함수
import { addItem, removeItem, changeContent } from "../Slice/itemSlice";

function DiaryEditor(props) {
  // 수정 확인
  let isEdit = props.isEdit;

  // 사이트 이동
  let navigate = useNavigate();

  // rudex 함수
  let dispatch = useDispatch();

  // 리스트
  let List = useSelector((state) => state.item.item);
  // 감정 리스트
  let emotionList = useSelector((state) => state.emotion);
  // 날씨 리스트
  let weatherList = useSelector((state) => state.weather);

  // 1. 날짜 저장
  // toISOString() 사용하고 slice를 이용해 년월일 표시
  //현재 위치한 시간(9시간을 현재 시간에 더해준 새로운 Date를 사용한다.)
  const offset = 1000 * 60 * 60 * 9;
  const time = new Date(new Date().getTime() + offset);
  let [date, setDate] = useState(time.toISOString().slice(0, 10));

  // 2. 감정 저장
  let [emotion, setEmotion] = useState(3);

  // 2-2. 날씨 저장
  let [weather, setWeather] = useState(3);

  // 3. 일기 텍스트 저장
  let [text, setText] = useState("");
  // 텍스트 확인
  let textInput = useRef();

  //  수정시 확인할  id(string으로 받아져서 parseInt로 변환)
  // url 아이디 확인
  let id = parseInt(props.id);

  // 수정시 불러올 데이터
  let [data, setData] = useState([]);

  useEffect(() => {
    // 생성페이지 타이틀 바꾸기
    let title = document.getElementsByTagName("title")[0];
    title.innerHTML = `나만의 일기장 - 생성`;
    // 수정할 시
    if (isEdit) {
      // id로 해당 전체 데이터 불러오기
      let data = List.find((e) => e.id === id);
      setData(data);

      //
      // 날짜 불러오기
      setDate(new Date(data.date).toISOString().slice(0, 10));
      //감정 불러오기
      setEmotion(data.emotion);
      // 날씨 불러오기
      setWeather(data.weather);
      // 일기 텍스트 불러오기
      setText(data.content);
      // 수정 페이지 타이틀 바꾸기
      let title = document.getElementsByTagName("title")[0];
      title.innerHTML = `나만의 일기장 - ${date} 수정`;
    }
    // isEdit값과 리스트 가져온다
  }, [isEdit, List]);
  //

  //
  return (
    <>
      {/* 헤더 */}
      <Header
        //뒤로가기와 제목 삭제
        text={isEdit ? `일기 수정 하기` : "새로운 일기 쓰기"}
        //과거
        leftChild={
          <Button
            onClick={() => {
              navigate("/diarylist");
            }}
            text={"< 뒤로가기"}
          />
        }
        // 삭제하기
        rightChild={
          isEdit ? (
            <Button
              onClick={() => {
                navigate("/diarylist");
                dispatch(removeItem(data.id));
              }}
              text={"삭제하기"}
              type={"myBtn_negative"}
            />
          ) : null
        }
      />
      {/* ---------------------------------- */}
      {/* 일기 작성란 */}
      <div className="new-wrap">
        {/* 날짜 선택 */}
        <div className="day-wrap">
          <h4>오늘의 날짜</h4>
          {/*  */}
          <input
            className="day"
            value={date}
            onChange={(e) => {
              setDate(e.target.value);
            }}
            type="date"
          />
          {/*  */}
        </div>
        {/* 감정 선택 */}
        <div className="emotion-wrap">
          <h4>오늘의 감정</h4>
          <div className="emotion-box-wrap">
            {emotionList.map((a, i) => {
              return (
                <EmotionItem
                  // 자식으로 부터 받은 id값을 이용해 emotion에 담는다?
                  // 부모가 자식에게 함수를 알려주고
                  onClick={(e) => {
                    setEmotion(e);
                  }}
                  // 선택한 emotion id가 emotion값과 같은지 확인
                  isSelect={emotionList[i].id === emotion}
                  i={i}
                  key={i}
                />
              );
            })}
          </div>
        </div>
        {/* 날씨 선택 */}
        <div className="weather-wrap">
          <h4>오늘의 날씨</h4>
          <div className="weather-box-wrap">
            {weatherList.map((a, i) => {
              return (
                <WeatherItem
                  // 자식으로 부터 받은 id값을 이용해 emotion에 담는다?
                  // 부모가 자식에게 함수를 알려주고
                  onClick={(e) => {
                    setWeather(e);
                  }}
                  // 선택한 weather id가 weather값과 같은지 확인
                  isSelect={weatherList[i].id === weather}
                  i={i}
                  key={i}
                />
              );
            })}
          </div>
        </div>
        {/* 일기 작성 */}
        <div className="text-wrap">
          <h4>오늘의 일기</h4>
          <div className="text-box">
            {/* onChange 변화시 */}
            <textarea
              ref={textInput}
              placeholder="오늘 하루 어땠나요? (최소 5글자 이상)"
              value={text}
              onChange={(e) => {
                setText(e.target.value);
              }}
            />
          </div>
        </div>
        {/* 버튼 (취소 ,  등록)  // 버튼 (취소 , 수정)*/}
        <div className="button-wrap">
          <Button
            onClick={() => {
              navigate("/diarylist");
            }}
            text={"취소"}
            type={"myBtn_negative"}
          />
          {isEdit ? (
            //수정 시
            <Button
              onClick={() => {
                // 텍스트확인 focus
                if (text.length < 5) {
                  textInput.current.focus();
                  return;
                }
                // 수정하기
                dispatch(
                  changeContent({
                    // 리스트 정렬을 보고
                    // 리스트 순서의 id값을 불러온다
                    id: data.id,
                    date: new Date(date).getTime(),
                    emotion: emotion,
                    weather: weather,
                    content: text,
                  })
                );
                // 사이트 이동(조회)
                navigate("/diarylist");
              }}
              text={"수정"}
              type={"myBtn_positive"}
            />
          ) : (
            // 등록 시
            <Button
              onClick={() => {
                // 텍스트확인 focus
                if (text.length < 5) {
                  textInput.current.focus();
                  return;
                }
                // 등록하기
                dispatch(
                  addItem({
                    date: new Date(date).getTime(),
                    emotion: emotion,
                    weather: weather,
                    content: text,
                  })
                );
                // 사이트 이동(조회)
                navigate("/diarylist");
              }}
              text={"등록"}
              type={"myBtn_positive"}
            />
          )}
        </div>
        {/*  */}
      </div>
      {/*  */}
    </>
  );
}

export default DiaryEditor;
