/*eslint-disable*/
// weather 전체

import { useSelector } from "react-redux";

function WeatherItem(props) {
  let i = props.i;
  let isSelect = props.isSelect;
  // 리스트
  let weather = useSelector((state) => state.weather);

  return (
    <div>
      <div
        // 부모 onclick함수를 넣어서 id값을 주고
        // id값을 넣고 함수 실행
        onClick={() => props.onClick(weather[i].id)}
        // 클래스 추가하기 (누른 값  on)
        className={`weatherItem ${isSelect ? `weather_on` : `weather_off`}`}
      >
        <img src={weather[i].img} />
        <p>{weather[i].text}</p>
      </div>
    </div>
  );
}

export default WeatherItem;
