/*eslint-disable*/
//변하는 일기 목록 정보를 관리할 상태가 필요하다.
// 이것을 Redux toolkit으로 구현한다.

//
import { configureStore, createSlice } from "@reduxjs/toolkit";
//
import storage from "redux-persist/lib/storage";
import { combineReducers } from "@reduxjs/toolkit";
import { persistReducer } from "redux-persist";

let itemSlice = createSlice({
  name: "item",
  initialState: [],
  //처음 상태

  // 상태를 업데이트 해줄 함수 모음
  reducers: {
    // 일기 추가
    addItem(state, action) {
      let newItem = action.payload;
      // id값 주기
      // 배열이 빈 배열이면 0
      // 배열이 있니 그러면 마지막값을 넣어
      // 리스트 마지막 값의 id+1
      let dataId = state.length === 0 ? 0 : state[state.length - 1].id + 1;

      // 배열의 맨 앞에 추가
      state.unshift({
        id: dataId,
        date: newItem.date,
        emotion: newItem.emotion,
        weather: newItem.weather,
        content: newItem.content,
      });
      return state;
    },

    // 일기 삭제
    removeItem(state, action) {
      let Item = action.payload;
      // 일기 정보 아이디를 불러와 삭제
      // Item : 삭제 누른 일기
      //
      // 삭제버튼을 누르면 그 버튼과 관련된 일기만 빼고
      // 다른 일기들만 남긴다

      let Remove = state.filter((state) => state.id !== Item);
      return Remove;
    },

    // 일기 수정
    // 텍스트를 받아오면
    // 그 선택한 아이디의 컨텐츠에
    // 텍스트 삽입
    changeContent(state, action) {
      // state id와 수정할 id가 같으면
      // map을 이용해서 일일 확인하고 해당 id값을 찾고
      // state id에 해당하는 값 변경
      // console.log(action.payload);
      // console.log(...state);

      // 변수 다시 담음
      let item = action.payload;

      return state.map((e) =>
        e.id === item.id
          ? {
              ...e,
              date: item.date,
              emotion: item.emotion,
              weather: item.weather,
              content: item.content,
            }
          : e
      );

      // return state;
      //!!! 중요
    },

    // 순서 정렬
    changeOrder(state, actions) {
      // 오래된순
      if (actions.payload === "last") {
        state.sort((prev, cur) => {
          if (prev.date > cur.date) return 1;
          if (prev.date < cur.date) return -1;
        });
      } else {
        //   최신순
        return state.sort((prev, cur) => {
          if (prev.date < cur.date) return 1;
          if (prev.date > cur.date) return -1;
        });
      }
    },
    // 감정 별로 정렬
    changeEmoOrder(state, actions) {
      if (actions.payload === "bad") {
        // 나쁜 감정순
        return state.sort((prev, cur) => {
          if (prev.emotion < cur.emotion) return 1;
          if (prev.emotion > cur.emotion) return -1;
        });
      } else if (actions.payload === "basics") {
        return state.sort((prev, cur) => {
          if (prev.date < cur.date) return 1;
          if (prev.date > cur.date) return -1;
        });
      } else {
        // 좋은 감정순
        state.sort((prev, cur) => {
          if (prev.emotion > cur.emotion) return 1;
          if (prev.emotion < cur.emotion) return -1;
        });
      }
    },

    // id별로 정렬
    changeIdOrder(state) {
      state.sort((prev, cur) => {
        if (prev.id > cur.id) return 1;
        if (prev.id < cur.id) return -1;
      });
    },
  },
});

let emotionSlice = createSlice({
  name: "emotion",
  initialState: [
    {
      id: 1,
      img: process.env.PUBLIC_URL + "/image/emotion1.png",
      text: "매우 좋음",
    },
    {
      id: 2,
      img: process.env.PUBLIC_URL + "/image/emotion2.png",
      text: "좋음",
    },
    {
      id: 3,
      img: process.env.PUBLIC_URL + "/image/emotion3.png",
      text: "보통",
    },
    {
      id: 4,
      img: process.env.PUBLIC_URL + "/image/emotion4.png",
      text: "나쁨",
    },
    {
      id: 5,
      img: process.env.PUBLIC_URL + "/image/emotion5.png",
      text: "매우 나쁨",
    },
  ],
});

let weatherSlice = createSlice({
  name: "weather",
  initialState: [
    {
      id: 1,
      img: process.env.PUBLIC_URL + "/image/weather1.svg",
      text: "매우 맑음",
    },
    {
      id: 2,
      img: process.env.PUBLIC_URL + "/image/weather2.svg",
      text: "맑음",
    },
    {
      id: 3,
      img: process.env.PUBLIC_URL + "/image/weather3.svg",
      text: "흐림",
    },
    { id: 4, img: process.env.PUBLIC_URL + "/image/weather4.svg", text: "비" },
    { id: 5, img: process.env.PUBLIC_URL + "/image/weather5.svg", text: "눈" },
  ],
});

export let {
  addItem,
  removeItem,
  changeContent,
  changeOrder,
  changeEmoOrder,
  changeIdOrder,
} = itemSlice.actions;

//
// 로컬 저장
const persistConfig = {
  key: "diary",
  storage,
};
const reducers = combineReducers({
  item: itemSlice.reducer,
});
const persistedReducer = persistReducer(persistConfig, reducers);

export default configureStore({
  reducer: {
    item: persistedReducer,
    // item: itemSlice.reducer,
    emotion: emotionSlice.reducer,
    weather: weatherSlice.reducer,
  },

  // 오류-> object 형태의 값을 string 형태로 변환(JSON.stringify)하는데, 이 상황에서 변환이 불가능한 값을 전달했다는 에러
  // https://velog.io/@pest95/RTK-non-serializable-value-%EC%98%A4%EB%A5%98-%ED%95%B4%EA%B2%B0-%EB%B0%A9%EB%B2%95
  //  미들웨어 설정을 추가했다.
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({ serializableCheck: false }),
  // 기본 값이 true지만 배포할때 코드를 숨기기 위해서 false로 변환하기 쉽게 설정에 넣어놨다.
  devTools: true,
});
