// 자세히 보기 페이지

import Button from "../components/Button";
import Header from "../components/Header";

//useParams 현재 사이트 위치 알기 위함
import { useParams } from "react-router-dom";
//
import { useSelector } from "react-redux";
//
import { useNavigate } from "react-router-dom";
//
//
import "../css/detail.css";
import { useEffect, useState } from "react";

function Detail() {
  // 현재사이트 id값
  let { id } = useParams();
  let dataId = parseInt(id);

  // 리스트
  let List = useSelector((state) => state.item.item);
  // // 감정 리스트
  let emotionList = useSelector((state) => state.emotion);
  // // 날씨 리스트
  let weatherList = useSelector((state) => state.weather);

  // 리스트 다시 담는곳.
  let [data, setData] = useState([]);
  // 감정 다시 담는곳
  let [emotion, setEmotion] = useState([]);
  // 날씨 다시 담는곳
  let [weather, setWeather] = useState([]);

  // 사이트 이동
  let navigate = useNavigate();

  useEffect(() => {
    // id로 해당 전체 데이터 불러오기
    let data = List.find((e) => e.id === dataId);
    let emotion = emotionList.find((e) => e.id === data.emotion);
    let weather = weatherList.find((e) => e.id === data.weather);
    // 리스트 담아준다
    setData(data);
    // 감정 리스트 담아준다
    setEmotion(emotion);
    // 날씨 리스트 담아준다
    setWeather(weather);

    // isEdit값과 리스트 가져온다
  }, [List, emotionList, weatherList, dataId]);

  useEffect(() => {
    // 페이지 타이틀 바꾸기
    let title = document.getElementsByTagName("title")[0];
    title.innerHTML = `나만의 일기장 - ${new Date(
      data.date
    ).toLocaleDateString()}`;
  }, [data]);

  // 데이터 없을시
  if (data === undefined) {
    return (
      <div className="notdata-wrap">
        <h4>없는 일기 입니다</h4>
        <Button
          onClick={() => {
            navigate("/");
          }}
          text={"홈으로"}
        />
      </div>
    );
  }
  //
  // 데이터 있을시
  else {
    return (
      <div className="detail-wrap">
        {/* 헤더 */}
        <Header
          //제목
          // 날짜가 오류가 난다.
          text={`${new Date(data.date).toLocaleDateString()} 기록`}
          //과거
          leftChild={
            <Button
              onClick={() => {
                navigate("/diarylist");
              }}
              text={"< 뒤로가기"}
            />
          }
          // 수정
          rightChild={
            <Button
              onClick={() => {
                navigate(`/edit/${data.id}`);
              }}
              text={"수정하기"}
            />
          }
        />
        {/*  */}
        {/* 날씨 감정 보여주기 */}
        <div className="item-wrap">
          {/* 감정 보여주기 */}
          <div className="detail-emotion-wrap">
            <h4>오늘의 감정</h4>
            <div className="emotion-img">
              <img src={emotion.img} alt="emotion" />
              <p>{emotion.text}</p>
            </div>
          </div>
          {/* 날씨 보여주기 */}
          <div className="detail-weather-wrap">
            <h4>오늘의 날씨</h4>
            <div className="weather-img">
              <img src={weather.img} alt="weather" />
              <p>{weather.text}</p>
            </div>
          </div>
        </div>

        {/* 작성한 일기 보여주기 */}
        <div className="detail-text-wrap">
          <h4>오늘의 일기</h4>
          <div className="text">
            <p>{data.content}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Detail;
