/*eslint-disable*/
/*리스트에 있는 일기 정보  */

import { useNavigate } from "react-router-dom";
import Button from "../components/Button";
import "../css/item.css";
import { useSelector } from "react-redux";

function Diaryitem(props) {
  let i = props.i;
  let data = props.data;

  // 사이트 이동
  let navigate = useNavigate();

  // 감정 리스트
  let emotionList = useSelector((state) => state.emotion);
  // id별로 감정점수 이미지
  let emotion = emotionList.find((e) => e.id === data[i].emotion);
  return (
    <div className="list-box">
      <div className="info-box">
        {/* 이미지(감정) */}
        <div
          className="img-bg"
          onClick={() => {
            navigate(`/detail/${data[i].id}`);
          }}
        >
          <img src={emotion.img} alt="img" />
        </div>
        {/* 날짜와 텍스트 */}
        <div
          className="content-box"
          onClick={() => {
            navigate(`/detail/${data[i].id}`);
          }}
        >
          <p className="day">
            {/* 년월일까지 보임 */}
            {new Date(data[i].date).toLocaleDateString()}
          </p>
          {/* 0~10까지 문자열 보임 */}
          <p>{data[i].content.slice(0, 10)}</p>
        </div>
        {/*수정 버튼 */}
        <div className="btn-wrap">
          <Button
            text={"수정하기"}
            type={"myBtn"}
            onClick={() => {
              navigate(`/edit/${data[i].id}`);
              // List.item[i].id
            }}
          />
        </div>
      </div>
    </div>
  );
}

export default Diaryitem;
